import { Api } from './base/api';
export class AuctionAPI extends Api {
    constructor(net, baseUrl, options) {
        super(baseUrl, options);
        this.net = net;
    }
    getLotList() {
        return this.get('/product').then((data) => {
            return data.items.map((item) => (Object.assign(Object.assign({}, item), { image: this.net + item.image })));
        });
    }
    getLotItem(id) {
        return this.get(`/product/${id}`).then((item) => (Object.assign(Object.assign({}, item), { image: this.net + item.image })));
    }
    orderLots(order) {
        return this.post('/order', order).then((data) => data);
    }
}
